.root {
    overflow: hidden;
    height: 150px;
    background-color: #ccc;

    &.hasAudio {
        background-color: transparent;
        height: auto;
    }
}

.player {
    display: block;
    overflow: hidden;

    iframe {
        display: block;
        width: 100%;
        height: 100%;
    }

    &.mixcloud {
        height: 118px !important;

        iframe {
            height: 120px !important;
            width: calc(100% + 2px) !important;
            position: relative;
            top: -1px;
            left: -1px;
        }
    }
}
