.root {
}

.inList {
    margin: 16px 0;
}

.clickablePagination {
    position: unset !important;
    line-height: 30px;
}

.bullet {
    cursor: pointer;
    transition: opacity 0.3s ease;

    &:not(.activeBullet):hover {
        opacity: 0.5;
    }
}

.activeBullet {
    background-color: #333;
    opacity: 1;
}

.videoContainer {
    width: 100%;
    overflow: hidden;
    background-color: #ccc;
    position: relative;
    padding-top: calc((9 / 16) * 100%);
    //box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);

    &.hasVideo {
        background-color: transparent;
    }

    &.square {
        padding-top: 100%;
    }

    &.vertical {
        padding-top: calc((16 / 9) * 100%);
    }

    &.yes {
        padding-left: 0;
        padding-right: 0;
    }
}

.player {
    position: absolute;
    top: 0;
    left: 0;
}

.annotation {
    position: absolute;
    background: rgba(0, 0, 0, 0.4);
    width: 100%;
    opacity: 1;
    bottom: 0;
    padding: 8px 16px;
    color: #eee;
    text-align: center;
    pointer-events: none;
}

.preview {
    padding: 0 8px;

    .placeholder {
        width: 100%;
        background-color: #ccc;
        border-radius: 16px;

        &.square {
            padding-top: 100%;
        }

        &.rectangle {
            padding-top: 56.25%;
        }
    }
}
