.wrapper {
}

.root {
    border-radius: 16px;
    overflow: hidden;
    background-color: #ccc;
    position: relative;
    padding-top: calc((9 / 16) * 100%);
    //box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);

    &.hasVideo {
        background-color: transparent;
    }

    &.square {
        padding-top: 100%;
    }

    &.vertical {
        padding-top: calc((16 / 9) * 100%);
    }

    &.yes {
        padding-left: 0;
        padding-right: 0;
    }
}

.player {
    position: absolute;
    top: 0;
    left: 0;
}
