@use "sass:math";

.root {
}

.blurred {
    filter: blur(20px);
}

.slide {
    margin: auto 0;
}

.clickablePagination {
    position: unset !important;
    line-height: 30px;
}

.bullet {
    cursor: pointer;
    transition: opacity .3s ease;

    &:not(.activeBullet):hover {
        opacity: .5;
    }
}

.activeBullet {
    background-color: #333;
    opacity: 1;
}

.imageBlock {
    overflow: hidden;
    //box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);

    .annotation {
        position: absolute;
        background: rgba(0, 0, 0, 0.4);
        width: 100%;
        opacity: 1;
        bottom: 0;
        padding: 8px 16px;
        color: #eee;
        text-align: center;
        pointer-events: none;
    }
}

.imageContainer {
    display: block;
    position: relative;

    .preloader {
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
    }
}

.placeholder {
    background-color: #ccc;

    &.original .imageContainer {
        pointer-events: none;
        padding-top: math.div(100%, 16) * 9;
    }
}

.original {
    position: relative;
    width: 100%;

    .imageContainer {
        cursor: pointer;

        .preloader {
            display: block;
            width: 100%;
            padding-top: calc(100% * (9 / 16));
            position: relative;
        }
    }

    img {
        display: none;
        width: 100%;
        height: auto;
        object-fit: none;

        &.loaded {
            display: block;
        }
    }
}

.square {
    position: relative;
    width: 100%;

    .imageContainer {
        cursor: pointer;
        width: 100%;
        padding-top: 100%;
    }

    .image {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}

.rectangle {
    position: relative;
    width: 100%;

    .imageContainer {
        cursor: pointer;
        width: 100%;
        padding-top: math.div(100%, 16) * 9;
    }

    .image {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}

.image > img {
    display: block;
    opacity: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: opacity .6s ease;

    &.loaded {
        opacity: 1;
    }
}

.fit-inside .image > img {
    object-fit: contain;
}


.fit-outside .image > img {
    object-fit: cover;
}

.stretch .image > img {
    object-fit: none;
}

.fill .image > img {
    object-fit: fill;
}

.perView-1 {
}

.perView-2 {
    font-size: 50%;

    .annotation {
        padding: 4px 8px;
    }
}

.perView-3 {
    font-size: 33.3%;

    .annotation {
        padding: 4px 8px;
    }
}

.perView-4,
.perView-5 {
    font-size: 5px;

    .annotation {
        padding: 4px 8px;
    }
}

.preview {
    padding: 0 8px;

    .placeholder {
        width: 100%;
        background-color: #ccc;
        border-radius: 16px;

        &.original {
            padding-top: 60%;
        }

        &.square {
            padding-top: 100%;
        }

        &.rectangle {
            padding-top: 56.25%;
        }
    }
}
